import React from "react";
import { useTranslation } from "react-i18next";

import "./FhaLoanRates.css";

function FhaLoanRates() {
  const { t } = useTranslation();
  return (
    <div
      id="fha-loan-rates"
      className="fha-loan-rates dark-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-5.png"
              alt="/images/loan-guide/s-5.png"
            />
            <p className="header-text-3">{t("fhaLoan.section_5.head")}</p>
            <h4 className="header-text-1">{t("fhaLoan.section_5.sub")}</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("fhaLoan.section_5.txt_1")}
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1">{t("fhaLoan.section_5.txt_2")}</p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <a href="/mortgage-rates" className="sub-text-1">
              <b>{t("fhaLoan.section_5.txt_3")}</b>
              {t("fhaLoan.section_5.txt_4")}
            </a>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>{t("fhaLoan.back_to_top")}</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FhaLoanRates;
